<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">사용자 관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">사용자</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row" v-show = "viewModel.isShowUserCreate"> 
        <div class="col-lg-12">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="card-title text-black">사용자 추가</h5></li>
                <li class="list-inline-item float-right"><button type="button" v-on:click="viewModel.isShowUserCreate=false" class="btn btn-warning"><i class="mdi mdi-window-close mr-2"></i> 생성창 닫기</button></li>
              </ul>
            </div>
            <div class="card-body">
               <form>
                <div class="form-group row">
                  <label for="inputEmail3" class="col-sm-2 col-form-label">아이디</label>
                  <div class="col-sm-8">
                    <input type="text" class="form-control" id="inputSuccess1" v-model="viewModel.detailUser.user_id" placeholder="사용자 아이디를 입력해주세요." v-bind:readonly="viewModel.isEnableUpdate" v-on:change="fnChangeValue('user')">
                  </div>
                  <div class="col-sm-2" v-show = "!viewModel.isEnableUpdate">
                    <button type="button" class="btn btn-outline-dark" v-on:click="fnOnClickCheckID()">아이디 중복확인</button>
                  </div>
                </div>
                <div class="form-group row" v-show = "!viewModel.isEnableUpdate" >
                  <label for="inputEmail3" class="col-sm-2 col-form-label">비밀번호</label>
                  <div class="col-sm-5">
                    <input type="password" class="form-control" id="inputSuccess2" v-model="viewModel.pwd1" placeholder="사용자 비밀번호를 입력해주세요.">
                  </div>
                  <div class="col-sm-5">
                    <input type="password" class="form-control" id="inputSuccess3" v-model="viewModel.pwd2" placeholder="비밀번호를 한번 더 입력해주세요.">
                  </div>
                </div>
                <div class="form-group row">
                  <label for="inputEmail3" class="col-sm-2 col-form-label">이름</label>
                  <div class="col-sm-10">
                    <input type="text" class="form-control" id="inputSuccess4" v-model="viewModel.detailUser.user_name" placeholder="사용자 이름을 입력해주세요.">
                  </div>
                </div>
                <div class="form-group row">
                  <label for="inputEmail3" class="col-sm-2 col-form-label">핸드폰번호</label>
                  <div class="col-sm-4">
                    <select class="form-control" id="formControlSelect" v-model="viewModel.phone1">
                      <option value="">국번 선택</option>
                      <option v-for="code in viewModel.listPhoneCodes" :key="code.sys_code" :value="code.sys_code">{{code.sys_title}}</option>
                    </select>
                  </div>
                  <div class="col-sm-3">
                    <input type="text" class="form-control" id="inputSuccess6" v-model="viewModel.phone2"  placeholder="phone2">
                  </div>
                  <div class="col-sm-3">
                    <input type="text" class="form-control" id="inputSuccess7" v-model="viewModel.phone3"  placeholder="phone3">
                  </div>
                </div>
                <div class="form-group row">
                  <label for="inputEmail3" class="col-sm-2 col-form-label">이메일</label>
                  <div class="col-sm-8">
                    <input type="text" class="form-control" id="inputSuccess8" v-model="viewModel.detailUser.user_email" placeholder="사용자 이메일을 입력해주세요." v-on:change="fnChangeValue('email')" v-bind:readonly="viewModel.isEnableUpdate">
                  </div>
                  <div class="col-sm-2" v-show = "!viewModel.isEnableUpdate">
                    <button type="button" class="btn btn-outline-dark" v-on:click="fnOnClickCheckEmail()">이메일 중복확인</button>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="inputEmail3" class="col-sm-2 col-form-label">사용자유형</label>
                  <div class="col-sm-10">
                    <select class="form-control" id="formControlSelect" v-model="viewModel.detailUser.user_type_code">
                      <option value="">사용자 유형 선택</option>
                      <option v-for="code in viewModel.listUserTypeCodes" :key="code.sys_code" :value="code.sys_code">{{code.sys_title}}</option>
                    </select>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="inputEmail3" class="col-sm-2 col-form-label">사용자 권한</label>
                  <div class="col-sm-4">
                    <div class="custom-control custom-checkbox" v-for = "(item,index) in viewModel.listRoles" :key="item.role_id">
                      <input class="custom-control-input" type="checkbox" :value="item.role_id" :checked="viewModel.checkedItem.indexOf(item.role_id) > -1" v-on:click="toggleSelection(item.role_id)" :id="index">
                      <label class="custom-control-label" :for="index"> {{item.role_title}}</label>
                    </div>
                  </div>
                </div>                
              </form>
            </div>
            <div class="card-footer bg-white">
              <div class="form-group row">
                <div class="col-sm-10" >
                  <button class="btn btn-danger" data-toggle="modal" data-target="#pwsChange" v-show= "viewModel.isEnableUpdate">비밀번호 변경</button>
                  <button type="submit" class="btn btn-success" v-show = "!viewModel.isEnableUpdate" v-on:click="fnClickBtnCreate()">생성</button>
                  <button type="submit" class="btn btn-success" v-show = "viewModel.isEnableUpdate" v-on:click="fnClickBtnCreate()">수정</button>
                  <button type="button" v-on:click="viewModel.isShowUserCreate=false" class="btn btn-warning">취소</button>
                </div>
              </div>
            </div>
          </div>
        </div>        
      </div>


      <div class="row"> 
        <div class="col-lg-12">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="mt-2 mb-0 text-black">사용자 목록</h5></li>
                <li class="list-inline-item float-right">
                  <button type="button" v-on:click="fnClickBtnShowCreateForm()" class="btn btn-success"> <i class="mdi mdi-plus mr-2"></i> 생성창 열기</button>
                </li>
              </ul>
            </div>
            <div class="card-body">
              <div class="table-responsive m-b-10">
                <table class="table">
                  <thead class="thead-dark">
                    <tr class="headings">
                      <th class="column-title">구분</th>
                      <th class="column-title">아이디</th>
                      <th class="column-title">이름</th>
                      <th class="column-title">이메일</th>
                      <th class="column-title">전화번호</th>
                      <th class="column-title">상태</th>
                      <th class="column-title">삭제</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for= "item in viewModel.list" :key="item.sys_code">
                      <td>
                        <select class="form-control" v-model= "item.user_type_code" v-on:change="fnChangeUserStat(item)">
                          <option v-for= "code in viewModel.listUserTypeCodes" :key="code.sys_code" v-bind:value= "code.sys_code">{{code.sys_title}}</option>
                        </select>
                      </td>
                      <td><button type="button" v-on:click="fnOnClickUpdateUser(item)" class="btn btn-link"> {{item.user_id}}</button></td>
                      <td>{{item.user_name}}</td>
                      <td>{{item.user_email}}</td>
                      <td>{{item.user_phone}}</td>
                      <td>
                        <select class="form-control" v-model="item.user_status_code" v-on:change="fnChangeUserStat(item)">
                          <option v-for= "code in viewModel.listUserStatusCodes" :key="code.sys_code" v-bind:value= "code.sys_code">{{code.sys_title}}</option>
                        </select>
                      </td>
                      <td><button type="button" v-on:click="fnClickDelete(item)" class="btn btn-outline-danger"> 삭제</button></td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <pager v-bind:pass="viewModel.pager"></pager>
            </div>
          </div>
        </div>        
      </div>

      <div id="pwsChange" class="modal fade">
        <div class="modal-dialog" role="dialog">
          <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title mt-0">비밀번호 변경</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">×</span>
                </button>
            </div>
            <div class="modal-body">
              <div class="form-group row">
                <label for="inputEmail3" class="col-sm-3 col-form-label">비밀번호</label>
                <div class="col-sm-9">
                  <input type="password" class="form-control" id="inputSuccess44" v-model="viewModel.new_pwd1" placeholder="변경 비밀번호를 입력해주세요.">
                </div>
              </div>
              <div class="form-group row">
                <label for="inputEmail3" class="col-sm-3 col-form-label">비밀번호 확인</label>
                <div class="col-sm-9">
                  <input type="password" class="form-control" id="inputSuccess45" v-model="viewModel.new_pwd2" placeholder="비밀번호를 한번 더 입력해주세요.">
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button class="btn btn-outline-success" v-on:click="fnClickBtnPwdUpdate()">수정</button>
              <button class="btn btn-outline-warning" data-dismiss="modal" aria-label="Close">취소</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import CompPager from './paging.vue';

export default{
    data: function () {
      return {
        viewModel: {
          totalCount: 0,
          list: [],
          listRoles: [],
          listUserTypeCodes: [],
          listUserStatusCodes: [],
          listPhoneCodes: [],
          detailUser: {user_type_code:''},
          pwd1: "",
          pwd2: "",
          new_pwd1: "",
          new_pwd2: "",
          phone1: "",
          phone2: "",
          phone3: "",
          isShowUserCreate: false,
          isEnableUpdate: false,
          checkedItem: [],
          isInitSystem: false,
          resultMessage: "",
          findOption: "user_name",
          findText: "",
          isShowMessage: false,
          isCheckedID: false,
          isCheckedEmail:false,
          regId : /^[A-Za-z0-9]{4,12}$/,
          regPass :  /^[A-Za-z0-9]{8,16}$/,
          regEmail :  /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i,
          regPhone :  /^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-[0-9]{3,4}-[0-9]{4}$/,

          pager: {
            model: {},
            pageNo: 1,
            totalSize: 0,
            rowSize: 10,
            blockSize: 5,
            list: [],
            callBack: function (n) {
              this.model.fnGetList();
             }
          }
        }
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
        console.log("mounted");
      try {
        this.fnGetList();
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
        fnGetList: function() {
          let vm = this.viewModel;
          Axios.post("/api/admin/user/manage", {pageNo:vm.pager.pageNo, pageSize:vm.pager.rowSize}).
          then(function(response) {
            let rs = response.data;

            vm.list=rs.list;
            vm.listUserTypeCodes=rs.listUserTypeCodes;
            vm.listUserStatusCodes=rs.listUserStatusCodes;
            vm.listPhoneCodes=rs.listPhoneCodes;
            vm.listRoles=rs.listRoles;
            vm.listSido=rs.listAdm;
            vm.isShowUserCreate = false;
            vm.isEnableUpdate = false;
            vm.pager.totalSize = rs.totalCount;
            vm.pageNum = (vm.pager.pageNo * vm.pager.rowSize) - vm.pager.rowSize;
            CompPager.props = CompPager.methods.createPaging(vm.pager, this, "list");
          }).catch(function(ex) {
              console.log(ex);
          });
      },

      //유저 생성
      fnClickBtnShowCreateForm: function(){
        let vm = this.viewModel;

        vm.isShowUserCreate=true;
        vm.isEnableUpdate=false;
        vm.detailUser={user_type_code:''};
        vm.phone1="";
        vm.phone2="";
        vm.phone3="";
      },

      //사용자 권한 선택
      toggleSelection: function(paramValue) {
        let vm = this.viewModel;
        let idx = vm.checkedItem.indexOf(paramValue);
        // is currently selected
        if (idx > -1) {
          vm.checkedItem.splice(idx, 1);
        }
        // is newly selected
        else {
          vm.checkedItem.push(paramValue);
        }
      },

      //아이디 중복
      fnOnClickCheckID:function(){
        let vm = this;

        if(vm.viewModel.detailUser.user_id == null || vm.viewModel.detailUser.user_id==""){
          swal("아이디를 입력하세요.");
          return false;
        }else{
          if (!vm.viewModel.regId.test(vm.viewModel.detailUser.user_id)) {
            swal("아이디는 영문,숫자를 포함한 4자이상12자 이하입니다.");
            return false;
          }else{
            Axios.post("/api/account/detail",{paramUserID:vm.viewModel.detailUser.user_id})
            .then(function(response) {
              if(response.data.detail==null){ 
                swal("사용 가능한 아이디 입니다.");
                vm.viewModel.isCheckedID=true;
              }
              else {
                swal("이미 사용중인 아이디 입니다.");
                vm.viewModel.isCheckedID=false;
              }
            });
          }
        }
      },

      //사용자 추가
      fnClickBtnCreate:function(){
        let vm = this;
        if(!vm.viewModel.isEnableUpdate && !vm.viewModel.isCheckedID){
          swal("아이디 중복체크를 하세요.");
          return false;
        }
        if(!vm.viewModel.isEnableUpdate && !vm.viewModel.isCheckedEmail){
          alert("이메일 중복체크를 하세요.");
          return false;
        }
        vm.viewModel.detailUser.user_roles = null;
        for(var i=0;i<vm.viewModel.checkedItem.length;i++){
          if(i==0) vm.viewModel.detailUser.user_roles=vm.viewModel.checkedItem[i];
          else vm.viewModel.detailUser.user_roles+=","+vm.viewModel.checkedItem[i];
          
        }
        if(!vm.viewModel.isEnableUpdate && (vm.viewModel.pwd1 == null || vm.viewModel.pwd1==""||vm.viewModel.pwd2 == null || vm.viewModel.pwd2=="")){
          swal("비밀번호를 입력하세요.");
          return false;
        }
        if(!vm.viewModel.isEnableUpdate && (vm.viewModel.pwd1 != vm.viewModel.pwd2 )){
          swal("비밀번호와 비밀번호확인이 다릅니다.");
          return false;
        }
        if (!vm.viewModel.isEnableUpdate && !vm.viewModel.regPass.test(vm.viewModel.pwd1)) {
          swal("비밀번호는 영문,숫자를 포함한 8자이상16자 이하입니다.");
          return false;
        }else{
          vm.viewModel.detailUser.user_pwd = vm.viewModel.pwd1;
        }
        if(vm.viewModel.detailUser.user_name == null || vm.viewModel.detailUser.user_name==""){
          swal("사용자 이름을 입력하세요.");
          return false;
        }
        if(vm.viewModel.detailUser.user_email == null || vm.viewModel.detailUser.user_email==""){
          swal("사용자 이메일을 입력하세요.");
          return false;
        }		
        if(!vm.viewModel.regEmail.test(vm.viewModel.detailUser.user_email)){
          swal("이메일 형식을 확인해주세요.");
          return false;
        }
        if(vm.viewModel.phone1 != null || !vm.viewModel.phone1==""){			
          if(!vm.viewModel.regPhone.test(vm.viewModel.phone1+"-"+vm.viewModel.phone2+"-"+vm.viewModel.phone3)){
            swal("연락처 형식을 확인해주세요.");
            return false;
          }else{
            vm.viewModel.detailUser.user_phone = vm.viewModel.phone1+"-"+vm.viewModel.phone2+"-"+vm.viewModel.phone3;
          }
        }
        if(vm.viewModel.detailUser.user_type_code == null || vm.viewModel.detailUser.user_type_code==""){
          swal("사용자 유형을 선택하세요.");
          return false;
        }
        if(vm.viewModel.detailUser.user_roles == null || vm.viewModel.detailUser.user_roles==""){
          swal("사용자 권한을 선택하세요.");
          return false;
        }
        if(!vm.viewModel.isEnableUpdate ){
            Axios.post("/api/account/create",{detail:vm.viewModel.detailUser})
          .then(function(response) {
            if(response.data.resultCount==0){
              swal("비밀번호는 영문,숫자를 포함한 8자이상16자 이하입니다.");
            }else{
              swal("등록되었습니다.");
              vm.fnGetList();
            }
            
          });
        }else{
            Axios.post("/api/account/update",{detail:vm.viewModel.detailUser})
          .then(function(response) {
            swal("수정되었습니다.");
            vm.fnGetList();
          });
        }
      },

      //사용자 삭제
      fnClickDelete: function(paramItem){
        let vm = this;
        Axios.post("/api/account/delete",{paramUserID:paramItem.user_id})
        .then(function(response) {
          vm.fnGetList();
          swal("삭제되었습니다.");
        });
      },
      
      //사용자 조회
      fnOnClickUpdateUser: function(paramItem){
        let vm = this.viewModel;
        vm.isShowUserCreate=true;
        vm.isEnableUpdate=true;
        vm.checkedItem=[];
        
        Axios.post("/api/account/detail",{paramUserID:paramItem.user_id})
        .then(function(response) {
          vm.detailUser=response.data.detail;
          if(response.data.detail.user_roles!=null&&response.data.detail.user_roles!="")
            vm.checkedItem=response.data.detail.user_roles.split(",");
          if(response.data.detail.user_phone!=null&&response.data.detail.user_phone!=""){
            vm.phone1=response.data.detail.user_phone.split("-")[0];
            vm.phone2=response.data.detail.user_phone.split("-")[1];
            vm.phone3=response.data.detail.user_phone.split("-")[2];
          }
        });
      },

      //비밀번호 변경
      fnClickBtnPwdUpdate: function(){
        let vm = this;
        if(vm.viewModel.new_pwd1 != vm.viewModel.new_pwd2 ){
          swal("비밀번호와 비밀번호확인이 다릅니다.");
          return false;
        }
        if (!vm.viewModel.isEnableUpdate && !vm.viewModel.regPass.test(vm.viewModel.new_pwd1)) {
          swal("비밀번호는 영문,숫자를 포함한 8자이상16자 이하입니다.");
          return false;
        }
        vm.viewModel.detailUser.user_pwd = vm.viewModel.new_pwd1;
        Axios.post("/api/account/pwd/update",{detail:vm.viewModel.detailUser})
        .then(function(response) {
          if(response.data.resultCount>0){
            swal("비밀번호가 변경되었습니다.");
            //vm.fnGetList();
            $(".close").trigger("click");
          }
        });
      }

      //사용자 권한 변경
      ,fnChangeUserStat : function(paramItem){
        Axios.post("/api/admin/user/type/update",{
          paramUserID:paramItem.user_id,
          paramUserType:paramItem.user_type_code,
          paramUserStatus:paramItem.user_status_code
        }).then(function(data){
          if(data.resultCount<=0){
            alert("오류가 발생하였습니다.");   
          }
        });
      }

      ,fnOnClickCheckEmail:function(){
        let vm = this;
        if(vm.viewModel.detailUser.user_email == null || vm.viewModel.detailUser.user_email == ""){
            alert("이메일을 입력하세요");
            return false;
        }else{
          Axios.post("/api/account/detail/email",{paramUserEmail:vm.viewModel.detailUser.user_email })
          .then(function(response) {
              if(response.data.detail==null){ 
                  alert("사용 가능한 이메일 입니다.");
                  vm.viewModel.isCheckedEmail=true;
              }
              else {
                  alert("이미 사용중인 이메일 입니다.");
                  vm.viewModel.isCheckedEmail=false;
              }
          });
        }
      }

      ,fnChangeValue: function(paramItem){
        let vm = this;
        if(paramItem == 'user'){
          vm.viewModel.isCheckedID = false;
        }else{
          vm.viewModel.isCheckedEmail = false;
        }
      }
    }
  }
</script>




