<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">코드관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">코드</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row"> 
        <div class="col-lg-6">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="mt-2 mb-0 text-black">그룹 코드</h5></li>                
              </ul>
            </div>
            <div class="card-body">
              <div class="table-responsive m-b-10">
                <table class="table">
                  <thead class="thead-dark">
                    <tr class="headings">
                      <th class="column-title">코드그룹</th>
                      <th class="column-title">코드 그룹명</th>
                      <th class="column-title">삭제</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for = "item in viewModel.listGrps" :key="item.grp_code">
                      <td>
                        <a href="#" v-on:click ="fnSelectedGrp(item)" ><i v-bind:class="{'mdi mdi-check-circle mr-2':item.selected}"></i> {{item.grp_code}}</a>
                      
                      </td>
                      <td>{{item.grp_title}}</td>
                      <td>
                        <button type="button" v-on:click ="fnDelteGrpCode(item)" class="btn btn-outline-danger"> 삭제</button>
                      </td>
                    </tr>
                    <tr>
                      <td><input type="text" v-model="viewModel.inputGrpCode" maxlength="8" /></td>
                      <td><input type="text" v-model="viewModel.inputGrpCodeTxt" /></td>
                      <td>
                        <button type="button" v-on:click="fnClickAddGrpCode()" class="btn btn-outline-success"> 추가</button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-6">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="mt-2 mb-0 text-black">상세 코드</h5></li>                
              </ul>
            </div>
            <div class="card-body">
              <div class="table-responsive m-b-10">
                <table class="table">
                  <thead class="thead-light">
                    <tr class="headings">
                      <th class="column-title">코드</th>
                      <th class="column-title">코드명</th>
                      <th class="column-title">삭제</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in viewModel.listSysCodes" :key="item.sys_code">
                      <td>{{item.sys_code}}</td>
                      <td>{{item.sys_title}}</td>
                      <td>
                        <button type="button" v-on:click="fnDeleteSysCode(item)" class="btn btn-outline-danger"> 삭제</button>
                      </td>
                    </tr>
                    <tr>
                      <td><input type="text" v-model="viewModel.inputSysCode" maxlength="8"/></td>
                      <td><input type="text" v-model="viewModel.inputSysCodeTxt" /></td>
                      <td>
                        <button type="button" v-on:click="fnClickAddSysCode()" class="btn btn-outline-success"> 추가</button>
                      </td>
                    </tr>
                  </tbody>
                </table>                                                                                       
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'

export default{
    data: function () {
      return {
        viewModel: {
          listGrps: [],
          listSysCodes: [],
          inputGrpCode: "",
          inputGrpCodeTxt: "",
          inputSysCode: "",
          inputSysCodeTxt: "",
          selectedGrp: null
        }
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
        console.log("mounted");
      try {
        this.fnGetListGrpCode();
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
        fnGetListGrpCode: function() {
            let vm = this;
            Axios.post("/api/admin/code/grp/list", {}).
            then(function(response) {
                vm.viewModel.listGrps = response.data.listCodeGrp;
                for(var i=0; i<vm.viewModel.listGrps.length; i++){
                    vm.viewModel.listGrps[i].selected = false;
                }
            }).catch(function(ex) {
                console.log(ex);
            });
        },

        fnGetListSysCodes: function(paramGrpCode) {
            let vm = this;
            if(paramGrpCode!=""){
                Axios.post("/api/admin/code/sys/list", {paramCodeGrp: vm.viewModel.selectedGrp.grp_code})
                .then(function(response) {
                    vm.viewModel.listSysCodes = response.data.listCodeSys;
                }).catch(function(ex) {
                    console.log(ex);
                });
            }   
        },

        //그룹코드 추가 버튼 클릭
        fnClickAddGrpCode: function() {
            let vm = this;
            if(vm.viewModel.inputGrpCode==""||vm.viewModel.inputGrpCodeTxt==""){
                alert("모든 필드는 필수 입력 항목 입니다.","");
                return false;
            }
            this.fnCreateGrpCode();
        },

        //시스템코드 추가
        fnClickAddSysCode: function() {
            let vm = this;
            if(vm.viewModel.selectedGrp==null){
                alert("그룹코드를 선택하세요.","");
                return false;
            }
            if(vm.viewModel.inputSysCode == null || vm.viewModel.inputSysCode==""){
                alert("코드를 입력하세요.","");
                return false;
            }
            if(vm.viewModel.inputSysCodeTxt == null || vm.viewModel.inputSysCodeTxt==""){
                alert("코드명을 입력하세요.","");
                return false;
            }
            this.fnCreateSysCode();            
        },

        //그룹코드 추가
        fnCreateGrpCode: function() {
            let vm = this;       
            Axios.post("/api/admin/code/grp/create", {
                paramCodeGrp: vm.viewModel.inputGrpCode,
                paramCodeGrpTxt: vm.viewModel.inputGrpCodeTxt
            }).then(function(response) {
                vm.viewModel.inputGrpCode = "";
                vm.viewModel.inputGrpCodeTxt = "";
                vm.viewModel.listGrps = response.data.listCodeGrp;
            }).catch(function(ex) {
                console.log(ex);
            });
              
        },

        //시스템코드 추가
        fnCreateSysCode: function() {
            let vm = this;
            Axios.post("/api/admin/code/sys/create", {
                paramCodeGrp: vm.viewModel.selectedGrp.grp_code,
                paramSysCode: vm.viewModel.inputSysCode,
                paramSysCodeTxt: vm.viewModel.inputSysCodeTxt
            }).then(function(response) {
                vm.viewModel.inputSysCode = "";
                vm.viewModel.inputSysCodeTxt = "";
                vm.viewModel.listSysCodes = response.data.listCodeSys;
            }).catch(function(ex) {
                console.log(ex);
            });
        },

        //코드 삭제
        fnDelteGrpCode: function(paramItem) {
            let vm = this;
            Axios.post("/api/admin/code/grp/drop", {
                paramCodeGrp: paramItem.grp_code
            }).then(function(response) {
                vm.viewModel.listGrps = response.data.listCodeGrp;
            }).catch(function(ex) {
                console.log(ex);
            });  
        },

        fnDeleteSysCode: function(paramItem) {
            let vm = this;
            Axios.post("/api/admin/code/sys/drop", {
                paramCodeGrp: paramItem.grp_code,
                paramSysCode: paramItem.sys_code
            }).then(function(response) {
                vm.viewModel.listSysCodes=response.data.listCodeSys;
            }).catch(function(ex) {
                console.log(ex);
            });  
        },

        fnSelectedGrp: function(paramItem) {
            let vm = this;
            
            for(var i=0; i< vm.viewModel.listGrps.length; i++){
                vm.viewModel.listGrps[i].selected = false;
            }
            paramItem.selected=true;
            vm.viewModel.selectedGrp = paramItem;     
                        
            this.fnGetListSysCodes(paramItem.grp_code);
        }
    }
  }
</script>




