<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">메뉴 권한관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">메뉴관리</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row"> 
        <div class="col-lg-12">
          <div class="card m-b-10">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="mt-2 mb-0 text-black">메뉴 권한관리</h5></li>
              </ul>
            </div>
            <div class="card-body">             
              <div class="table-responsive m-b-10">
                <table class="table">
                  <thead class="thead-dark">
                    <tr class="headings">
                      <th class="column-title">메뉴 아이디</th>
                      <th class="column-title">메뉴명</th>
                      <th class="column-title">그룹</th>
                      <th class="column-title" v-for="item in viewModel.listRoles" :key="item.role_id">{{item.role_title}}</th>
                      <th>저장</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in viewModel.listMenu" :key="item.menu_id">
                      <td>{{item.menu_id}}</td>
                      <td>{{item.menu_title}}</td>
                      <td>{{item.menu_group}}</td>
                      <td v-for="(role,index2) in viewModel.listRoles" :key="role.role_id">
                        <div class="custom-control custom-checkbox custom-control-inline">                      
                          <input class="custom-control-input" type="checkbox" :id="index2" value="Y" name="detailContent.fixed_yn" v-model="item.menuRoles[index2].read_yn" true-value="Y" false-value="N">
                          <label class="custom-control-label" :for="index2">읽기</label>
                        </div>
                        <div class="custom-control custom-checkbox custom-control-inline">                      
                          <input class="custom-control-input" type="checkbox" :id="index2+100" value="Y" name="detailContent.fixed_yn" v-model="item.menuRoles[index2].write_yn" true-value="Y" false-value="N">
                          <label class="custom-control-label" :for="index2+100">쓰기</label>
                        </div>
                      </td>
                      <td><button type="button" v-on:click="fnCreateRoleAction(item)" class="btn btn-outline-success"> 저장</button></td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>        
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Common from './common.js';

export default{
    data: function () {
      return {
        viewModel: {
          listMenu: [],
          listRoles: []
        }
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
        console.log("mounted");
      try {
        this.fnPageRoleInit();
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
      fnPageRoleInit: function() {
        let vm = this;
        Axios.post("/api/admin/menu/role", {})
        .then(function(response) {
          vm.viewModel.listMenu=response.data.listMenu;
          vm.viewModel.listRoles=response.data.listRole;

          console.log(vm.viewModel.listMenu);

          for(let i=0;i<vm.viewModel.listMenu.length;i++){
            vm.viewModel.listMenu[i].menuRoles=[];            
            for(let k=0;k<vm.viewModel.listRoles.length;k++){
              let temp={role_id:'',write_yn:'N',read_yn:'N'};              
              if(vm.viewModel.listMenu[i].menu_role==null || vm.viewModel.listMenu[i].menu_role==''){
                temp.role_id=vm.viewModel.listRoles[k].role_id;
              }else{
                let mRoles=vm.viewModel.listMenu[i].menu_role.split(',');
                let _index=mRoles.indexOf(vm.viewModel.listRoles[k].role_id);
                if(_index>-1){
                  temp.role_id=vm.viewModel.listRoles[k].role_id;
                  temp.write_yn=vm.viewModel.listMenu[i].write_yn==null?"":vm.viewModel.listMenu[i].write_yn.split(',')[_index];
                  temp.read_yn='Y';
                }else{
                  temp.role_id=vm.viewModel.listRoles[k].role_id;
                }
              }
              vm.viewModel.listMenu[i].menuRoles.push(temp);
            }
          }
        });
      }

      // 저장
      ,fnCreateRoleAction: function(item){
        let _roles=[];
        let _write=[];
        for(let i=0;i<item.menuRoles.length;i++){
          if(item.menuRoles[i].read_yn=='Y'){
            _roles.push(item.menuRoles[i].role_id);
            if(item.menuRoles[i].write_yn=='Y'){
              _write.push('Y');
            }else{
              _write.push('N');
            }
          }
        }
        Axios.post("/api/admin/menu/role/update", {paramMenuID:item.menu_id,paramRoles:_roles.join(','),paramWriteRoles:_write.join(',')})
        .then(function(response) {
          swal("저장되었습니다.");
        });
      }
    }
  }
</script>




