<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">게시물관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">게시판</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row"> 
        <div class="col-lg-12">
          <div class="card m-b-30">
            
            <div class="card-body">
              <form name="mainForm" id="mainForm" action="/api/admin/board/content/create/reply" method="post" enctype="multipart/form-data">
                <div class="form-group row">
                  <div class="col-sm-2">게시판</div>
                  <div class="col-sm-10">
                    <select class="form-control" v-model="bbsContentViewModel.detailContent.bbs_id" id="bbs_id" v-bind:disabled="true">
                      <option value="">게시판을 선택하세요</option>
                      <option v-for="item in bbsContentViewModel.listBBS" :key="item.bbs_id" :value="item.bbs_id">{{item.bbs_title}}</option>
                    </select>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="bbsId" class="col-sm-2 col-form-label">제목</label>
                  <div class="col-sm-10">
                    <input type="text" class="form-control" id="bbs_cont_title" v-model="bbsContentViewModel.detailContent.bbs_cont_title" placeholder="게시물의 제목을 입력하세요" v-bind:disabled="true">
                  </div>
                </div>   
                <div class="form-group row">
                  <label for="bbsTitle" class="col-sm-2 col-form-label">내용</label>
                  <div class="col-sm-10">
                    <div id="xp-summernote"></div>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="bbsId" class="col-sm-2 col-form-label">개별파일</label>
                  <div class="col-sm-10">
                    <div class="row">
                      <div class="col-form-label col-sm-1 pt-0"><button type="button" class="btn btn-outline-success" id="btnFileAdd"><i class="mdi mdi-plus"></i> </button></div>
                      <div class="col-sm-11"  id="fileLayer">
                        <div class="row">
                          <div class="form-group col-sm-10">
                            <input type="file" id="uploadFiles" name="uploadFiles" class="form-control">
                          </div>
                          <div class="form-group col-sm-2">
                            <button type="button" class="btn btn-outline-danger" id="btnFileDel"><i class="mdi mdi-delete-sweep"></i></button>
                          </div>
                        </div>                        
                      </div>
                    </div>
                  </div>
                </div>
                <input type="hidden" name="detailContent.bbs_id" id="bbsId">
                <input type="hidden" name="detailContent.bbs_seq" id="bbsSeq">
                <input type="hidden" name="detailContent.secret_yn" id="bbsSecret">
                <input type="hidden" name="detailContent.bbs_cont_title" id="bbsTitle">
                <input type="hidden" name="smartEditor" id="smartEditor">
                <input type="hidden" name="detailContent.file_grp_id" id="file_grp_id">
              </form> 
              <div class="form-group row">
                <label for="bbsTitle" class="col-sm-2 col-form-label">멀티파일</label>
                <div class="col-sm-10">
                  <button type="button" id="sbmtbtn" class="btn btn-secondary btn-sm btn-block" v-show="false">파일을 최종 선택 후 눌려주세요</button>
                  <form action="/api/file/upload" id="mydropzone" class="dropzone" method="post" enctype="multipart/form-data">
                    <div id="dropzonePreview"></div>
                    <input type="hidden" id="fileGroup" name="fileGroup"> 
                  </form>
                </div>
            </div>
            <div class="card-footer bg-white">
              <div class="form-group row">
                <div class="col-sm-10"> 
                    <button type="button" class="btn btn-success" v-on:click="onClickSave">등록</button>
                    <router-link to='/bbsContentList' class="btn btn-warning">취소</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Common from './common.js';

export default{
    data: function () {
      return {
        bbsContentViewModel: {
          detailContent: {bbs_id:''}
          , listBBS: []
          , listCategory:[]
        }
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
      //console.log("mounted[reply]");
      try {
        this.fnContentReplyInit(this.$route.params.bbsID,this.$route.params.seq,this.$route.params.title,this.$route.params.secret);
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
      fnContentReplyInit: function(paramID,paramSeq,paramTitle,paramSecret){
        let vm = this;
        
        $("#mydropzone").dropzone();
        $('#xp-summernote').summernote({
          height: 220,
          minHeight: null,
          maxHeight: null,
          focus: false ,
          lang: 'ko-KR'
        });

        Axios.post("/api/admin/board/list", {})
        .then(function(response) {
          let rs = response.data;
          vm.bbsContentViewModel.listBBS = rs.listMaster;
          vm.bbsContentViewModel.listCategory=response.data.listCategory;
          vm.bbsContentViewModel.detailContent.bbs_id=paramID;
          vm.bbsContentViewModel.detailContent.seq = paramSeq;
          vm.bbsContentViewModel.detailContent.secret_yn = paramSecret;
          vm.bbsContentViewModel.detailContent.bbs_cont_title = 'Re: ' + paramTitle;
        }).catch(function(ex) {
            console.log(ex);
        });
      }
      , onClickSave: function () {
        let vm = this;

        if(vm.bbsContentViewModel.detailContent.bbs_id == null || vm.bbsContentViewModel.detailContent.bbs_id == ""){
          alert("게시판을 선택하세요");
          return false;
        }
        if(vm.bbsContentViewModel.detailContent.bbs_cont_title == null || vm.bbsContentViewModel.detailContent.bbs_cont_title == ""){
          alert("제목을 입력하세요");
          return false;
        }
        $("#smartEditor").val($('#xp-summernote').summernote('code'));
        $("#file_grp_id").val($('#fileGroup').val());
        $("#bbsId").val($('#bbs_id').val());
        $("#bbsSeq").val(vm.bbsContentViewModel.detailContent.seq);
        $("#bbsSecret").val(vm.bbsContentViewModel.detailContent.secret_yn);
        $("#bbsTitle").val($('#bbs_cont_title').val());

        let fileUploadType = ['doc','xlsx','hwp','txt','jpg','gif','png','jpeg','xml','pdf','ppt','pptx','docx','xls','bmp','zip'];
        let fileBoo = true;
        for(let i=0; i<document.mainForm.uploadFiles.length; i++){
          let fileObj = document.mainForm.uploadFiles[i];
          if(fileObj.files.length>0){
            let fileName = fileObj.files[0].name;
            let files = fileName.split('.');
            let fileExt = files[files.length-1];
            if(fileUploadType.indexOf(fileExt) == -1){
              fileBoo = false;
            }
          }
        }
        if(fileBoo == false){
          alert('업로드 불가능한 파일형식이 있습니다.');
          return;
        }

        //console.log(vm.bbsContentViewModel);

        document.forms['mainForm'].submit();
        alert("등록되었습니다.");
        window.location="/system/manage/default#/bbsContentList";

      }
    }
  }
</script>


