<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">메뉴관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">메뉴관리</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row"> 
        <div class="col-lg-12">
          <div class="card m-b-10">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="mt-2 mb-0 text-black">메뉴관리</h5></li>
              </ul>
            </div>
            <div class="card-body">
              <div class="row">
                <div class="table-responsive m-b-10">
                  <table class="table">
                    <thead class="thead-dark">
                      <tr class="headings">
                        <th class="column-title">아이디</th>
                        <th class="column-title">이름</th>
                        <th class="column-title">그룹</th>
                        <th class="column-title">레벨</th>
                        <th class="column-title">정렬</th>
                        <th class="column-title">URI</th>
                        <th class="column-title">타입</th>
                        <!-- <th class="column-title">CSS</th>
                        <th class="column-title">IMG</th> -->
                        <th class="column-title">수정</th>
                        <th class="column-title">삭제</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for= "item in viewModel.listMenu" :key="item.menu_id">
                        <td><input type="text" class="form-control" v-model="item.menu_id" /></td>
                        <td><input type="text" class="form-control" v-model="item.menu_title" /></td>
                        <td><input type="text" class="form-control" v-model="item.menu_group" /></td>
                        <td><input type="text" class="form-control" v-model="item.menu_level" /></td>
                        <td><input type="text" class="form-control" v-model="item.menu_sort" /></td>
                        <td><input type="text" class="form-control" v-model="item.menu_uri" /></td>
                        <td><input type="text" class="form-control" v-model="item.menu_type_code" /></td>
                        <!-- <td><input type="text" class="form-control" v-model="item.menu_css" /></td>
                        <td><input type="text" class="form-control" v-model="item.menu_img" /></td> -->
                        <td>
                          <button type="button" class="btn btn-outline-warning" v-on:click="fnClickUpdate(item)"><i class="mdi mdi-upload"></i></button>
                        </td>
                        <td>
                          <button type="button" class="btn btn-outline-danger" v-on:click="fnClickDelete(item)"><i class="mdi mdi-delete-sweep"></i></button>
                        </td>
                      </tr>
                      <tr>
                        <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_id" /></td>
                        <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_title" /></td>
                        <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_group" /></td>
                        <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_level" /></td>
                        <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_sort" /></td>
                        <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_uri" /></td>
                        <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_type_code" /></td>
                        <!-- <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_css" /></td>
                        <td><input type="text" class="form-control" v-model="viewModel.detailMenu.menu_img" /></td> -->
                        <td colspan="2">
                          <button type="button" class="btn btn-outline-success" v-on:click="fnOnClickCreateAction()"><i class="mdi mdi-plus"></i> </button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>        
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Common from './common.js';

export default{
    data: function () {
      return {
        viewModel: {
          listMenu: [],
          detailMenu: {}
        }
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
        console.log("mounted");
      try {
        this.fnPageInit();
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
      fnPageInit: function() {
        let vm = this.viewModel;
        Axios.post("/api/admin/menu", {}).
        then(function(response) {
          vm.listMenu=response.data.listMenu;
        });
      },     

      //메뉴삭제
      fnClickDelete: function(item) {
        let vm = this.viewModel;
        Axios.post("/api/admin/menu/drop", {id:item.menu_id}).
        then(function(response) {
          vm.listMenu=response.data.listMenu;   
          swal("삭제되었습니다.");       
        });
      },

      //메뉴수정
      fnClickUpdate: function(item) {
        let vm = this.viewModel;
        Axios.post("/api/admin/menu/update", {detailMenu:item}).
        then(function(response) {
          vm.listMenu=response.data.listMenu;     
          swal("수정되었습니다.");            
        });
      },

      //메뉴 생성
      fnOnClickCreateAction: function(){
        let vm = this.viewModel;
        if(vm.detailMenu.menu_id==undefined || vm.detailMenu.menu_id==""){
          swal("입력항목을 확인하세요.");
          return false;
        }else if(vm.detailMenu.menu_title==undefined || vm.detailMenu.menu_title==""){
          swal("메뉴 이름을 확인하세요.");
          return false;
        }else if(vm.detailMenu.menu_uri==undefined || vm.detailMenu.menu_uri==""){
          swal("메뉴 URL을 확인하세요.");
          return false;
        }else if(vm.detailMenu.menu_type_code==undefined || vm.detailMenu.menu_type_code==""){
          swal("메뉴 타입을 확인하세요.");
          return false;
        }
        Axios.post("/api/admin/menu/create",{detailMenu:vm.detailMenu}).
        then(function(response) {
          vm.listMenu=response.data.listMenu;
          vm.detailMenu={};   
          swal("등록되었습니다.");                
        });
      }





    }
  }
</script>




