<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">게시물관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">게시판</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row"> 
        <div class="col-lg-12">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="mt-2 mb-0 text-black">게시물 목록</h5></li>
                <li class="list-inline-item float-right"><router-link to='/bbsContentWrite' class="btn btn-success"><i class="mdi mdi-pencil mr-2"></i> 등록창 열기</router-link></li>
              </ul>
            </div>
            <div class="card-body">
              <div class="row">
                <div class="col-sm-12 col-md-6">
                  <div class="dataTables_length" id="xp-default-datatable_length">
                    <label>
                      
                    </label>
                  </div>
                </div>
              <div class="col-sm-12 col-md-6"></div>
            </div>
            <div>
              <form>
                <div class="form-row">                  
                  <div class="form-group col-md-2">
                    <select name="xp-default-datatable_length" aria-controls="xp-default-datatable" class="form-control form-control" v-on:change="fnChangeBBS" v-model="bbsContentViewModel.bbsId">
                      <option v-for = "item in bbsContentViewModel.listBBS" :key="item.bbs_id" :value="item.bbs_id">{{item.bbs_title}}</option>
                    </select>
                  </div>
                  <div class="form-group col-md-2">
                    <input type="text" class="form-control" id="searchTitle" placeholder="제목 검색" v-model="bbsContentViewModel.searchTitle">
                  </div>
                  <div class="form-group col-md-2">
                    <input type="text" class="form-control" id="searchContent" placeholder="내용 검색" v-model="bbsContentViewModel.searchContent">
                  </div>
                  <div class="form-group col-md-2">
                    <a href="#" class="btn btn-info" v-on:click="fnChangeBBS" style="padding-top: 7px;padding-bottom: 7px;"><i class="fa fa-search mr-2"></i> 조회</a>
                  </div>
                  <div class="form-group col-md-4">
                  </div>
                </div>
              </form>
              <div class="table-responsive m-b-10">
                <table class="table">
                  <thead class="thead-dark">
                    <tr class="headings">
                      <th class="column-title">번호</th>
                      <th class="column-title">제목</th>
                      <th class="column-title">카테고리</th>
                      <th class="column-title">파일유무</th>
                      <th class="column-title">작성자</th>
                      <th class="column-title">작성일</th>
                      <th class="column-title">사용여부</th>
                      <th class="column-title">삭제</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in bbsContentViewModel.listContents" :key="item.bbs_seq" :class="{'table-info':item.fixed_yn=='Y'}">
                      <td>{{item.bbs_seq}}</td>
                      <td><router-link :to="{ name: 'bbsContentDetail', params: { bbsID: item.bbs_id , seq: item.bbs_seq }}" style="color:blue"><span v-show="item.bbs_level > 0">&#8627;</span> {{item.bbs_cont_title}}</router-link></td>
                      <td><span class="badge badge-default">{{item.category_title}}</span></td>
                      <td><i class="mdi mdi-file-multiple mr-2" v-show="item.file_grp_id!=''"></i></td>
                      <td>{{item.create_user_name}}</td>
                      <td>{{item.create_date}}</td>
                      <td>{{item.use_yn}}</td>
                      <td><button type="button" v-on:click="fnDeleteContent(item)" class="btn btn-outline-danger"> 삭제</button></td>
                    </tr>
                  </tbody>                  
                </table>                
              </div>
              <pager v-bind:pass="bbsContentViewModel.pager"></pager>
            </div>
          </div>
        </div>        
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import CompPager from './paging.vue';
import Common from './common.js';

export default{
    data: function () {
      return {
        bbsContentViewModel: {
          listBBS: [],
          listContents: [],
          bbsId:'BBS_001',
          searchTitle:'',
          searchContent:'',
          pager: {
            model: {},
            pageNo: 1,
            totalSize: 0,
            rowSize: 10,
            blockSize: 5,
            list: [],
            callBack: function (n) {
              this.model.fnListContent(null);
            }
          }
        }        
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
        console.log("mounted");
      try {
        this.fnGetList();
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
      fnGetList: function(){
        let vm = this;
        Axios.post("/api/admin/board/list", {})
        .then(function(response) {
          let rs = response.data;
          vm.bbsContentViewModel.listBBS = rs.listMaster;
          vm.fnListContent();
        }).catch(function(ex) {
            console.log(ex);
        });
      }
      ,fnListContent: function(paramBBS){        
        let vm = this;
        if(paramBBS==null)
          paramBBS = vm.bbsContentViewModel.bbsId;
        Axios.post("/api/admin/board/content/list", {id:paramBBS,pageNo:vm.bbsContentViewModel.pager.pageNo, pageSize:vm.bbsContentViewModel.pager.rowSize,searchTitle:vm.bbsContentViewModel.searchTitle,searchContent:vm.bbsContentViewModel.searchContent})
        .then(function(response) {
          let rs = response.data;
          vm.bbsContentViewModel.listContents = rs.listContent;
          for(var i=0;i<vm.bbsContentViewModel.listContents.length;i++){
            vm.bbsContentViewModel.listContents[i].create_date = Common.fnConvertDateStr(vm.bbsContentViewModel.listContents[i].create_date,'-');
          }
          vm.bbsContentViewModel.pager.totalSize = rs.totalCount;
          vm.bbsContentViewModel.pageNum = (vm.bbsContentViewModel.pager.pageNo * vm.bbsContentViewModel.pager.rowSize) - vm.bbsContentViewModel.pager.rowSize;
          CompPager.props = CompPager.methods.createPaging(vm.bbsContentViewModel.pager, vm, "list");
        }).catch(function(ex) {
            console.log(ex);
        });
      }
      ,fnChangeBBS:function(){
        let vm = this;
        vm.fnListContent(vm.bbsContentViewModel.bbsId);
      }

      ,fnDeleteContent:function(paramItem){
        let vm = this;
        Axios.post("/api/admin/board/content/delete",{id:paramItem.bbs_id,paramSeq:paramItem.bbs_seq})
        .then(function(response) {
          swal("삭제되었습니다.");
          vm.fnListContent(paramItem.bbs_id);
        });
      }
    }
  }
</script>




