<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">게시판관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">게시판</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row" v-show = "isCreateBox"> 
        <div class="col-lg-12">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="mt-2 mb-0 text-black">게시판 추가</h5></li>
                <li class="list-inline-item float-right"><button type="button" class="btn btn-warning" v-on:click="isCreateBox=false"><i class="mdi mdi-window-close mr-2"></i> 생성창 닫기</button></li>
              </ul>
            </div>
            <div class="card-body">
              <form name="mainForm" id="mainForm" >
                <div class="form-group row">
                  <label for="bbsId" class="col-sm-2 col-form-label">게시판 아이디</label>
                  <div class="col-sm-10">
                    <input type="text" class="form-control" id="bbs_id" v-model="bbsViewModel.selectedBoard.bbs_id" placeholder="게시판의 고유한 아이디(8자리)를 입력해주세요." v-bind:readonly="isUpdateButton" >
                  </div>
                </div>
                <div class="form-group row">
                  <label for="bbsTitle" class="col-sm-2 col-form-label">게시판 이름</label>
                  <div class="col-sm-10">
                    <input type="text" class="form-control" id="bbs_title" v-model="bbsViewModel.selectedBoard.bbs_title" placeholder="게시판의 이름을 입력해주세요.">
                  </div>
                </div>
                <div class="form-group row">
                  <label for="readRole" class="col-sm-2 col-form-label">읽기 권한</label>
                  <div class="col-sm-4">
                    <div class="custom-control custom-checkbox" v-for = "(item,index) in bbsViewModel.listRoles" :key="item.role_id">                      
                      <input class="custom-control-input" type="checkbox" :value="item.role_id" :checked="bbsViewModel.readRoleItem.indexOf(item.role_id) > -1" v-on:click="toggleReadSelection(item.role_id)" :id="index">
                      <label class="custom-control-label" :for="index">{{item.role_title}}</label>
                    </div>
                  </div>
                  <label for="writeRole" class="col-sm-2 col-form-label">쓰기 권한</label>
                  <div class="col-sm-4">
                    <div class="custom-control custom-checkbox" v-for = "(item,index) in bbsViewModel.listRoles" :key="item.role_id">                      
                      <input class="custom-control-input" type="checkbox" :value="item.role_id" :checked="bbsViewModel.writeRoleItem.indexOf(item.role_id) > -1" v-on:click="toggleWriteSelection(item.role_id)" :id="index+100">
                      <label class="custom-control-label" :for="index+100">{{item.role_title}}</label>
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="readRole" class="col-sm-2 col-form-label">말머리</label>
                  <div class="col-sm-10">
                    <div class="row">
                      <div class="col-form-label col-sm-1 pt-0"><button type="button" v-on:click="fnOnAddCategory()" class="btn btn-outline-success" id="btnCategoryAdd"><i class="mdi mdi-plus"></i> </button></div>
                      <div class="col-sm-11" id="categoryLayer">
                        <div class="row" v-for="(item,index) in bbsViewModel.selectedBoard.listCate" :key="item.category_seq">
                          <div class="form-group col-sm-10">
                            <input type="text" class="form-control" v-model="item.category_title">
                          </div>
                          <div class="form-group col-sm-2">
                            <button type="button" class="btn btn-outline-danger" v-on:click="fnOnDeleteCategory(index)" id="btnCategoryDel"><i class="mdi mdi-delete-sweep"></i></button>
                          </div>

                        </div>                        
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <div class="col-sm-2">해더 사용여부</div>
                  <div class="col-sm-10">
                    <div class="custom-control custom-checkbox">                      
                      <input class="custom-control-input" type="checkbox" id="headerCheck" v-model="isHeaderBox">
                      <label class="custom-control-label" for="headerCheck">사용</label>
                    </div>
                  </div>
                </div>
                <div class="form-group row" v-show="isHeaderBox">
                  <div class="col-sm-2"></div>
                  <div class="col-sm-10">
                    <div id="xp-summernote"></div>
                  </div>
                </div>
                <div class="form-group row">
                  <div class="col-sm-2">사용여부</div>
                  <div class="col-sm-10">
                    <select class="form-control" v-model="bbsViewModel.selectedBoard.use_yn">
                      <option value="Y" selected>사용</option>
                      <option value="N">미사용</option>
                    </select>
                  </div>
                </div>
              </form>  
            </div>
            <div class="card-footer bg-white">
              <div class="form-group row">
                <div class="col-sm-10">
                  <button type="button" class="btn btn-success" v-on:click="fnOnCreateAction" v-show="!isUpdateButton">생성</button>
                  <button type="button" class="btn btn-success" v-on:click="fnOnUpdateAction" v-show="isUpdateButton">수정</button>
                  <button type="button" class="btn btn-warning" v-on:click="isCreateBox=false">취소</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row"> 
        <div class="col-lg-12">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item"><h5 class="mt-2 mb-0 text-black">게시판 목록</h5></li>
                <li class="list-inline-item float-right"><button type="button" class="btn btn-success" v-on:click="isCreateBox=true,isUpdateButton=false"><i class="mdi mdi-plus mr-2"></i> 생성창 열기</button></li>
              </ul>
            </div>
            <div class="card-body">
              <div class="table-responsive m-b-10">
                <table class="table">
                  <thead class="thead-dark">
                    <tr class="headings">
                      <th class="column-title">번호</th>
                      <th class="column-title">아이디</th>
                      <th class="column-title">제목</th>
                      <th class="column-title">헤더</th>
                      <th class="column-title">말머리</th>
                      <th class="column-title">사용여부</th>
                      <th class="column-title">수정</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for = "(item,index) in bbsViewModel.listBBS" :key="item.bbs_id">
                      <td>{{index+1}}</td>
                      <td>{{item.bbs_id}}</td>
                      <td>{{item.bbs_title}}</td>
                      <td>{{item.header_yn}}</td>
                      <td>{{item.category_titles}}</td>
                      <td>{{item.use_yn}}</td>
                      <td><button type="button" class="btn btn-outline-primary" v-on:click="fnClickDetail(item)"> 수정</button></td>
                    </tr>
                  </tbody>
                </table>                
              </div>              
            </div>
          </div>
        </div>        
      </div>
    </div>
  </div>
</template>



<script>
import Axios from 'axios'

export default{
    data: function () {
      return {
        bbsViewModel: {
          listRoles: [],
          listBBS: [],
          readRoleItem: [],
          writeRoleItem: [],
          selectedBoard:{use_yn:'Y',listCate:[{category_title:''}]}
        }
        ,isCreateBox : false
        ,isHeaderBox : false
        ,isUpdateButton : false
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
        console.log("mounted");
      try {
        this.fnMngBBS();
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
      fnMngBBS: function(){
        $('#xp-summernote').summernote({
          height: 220,
          minHeight: null,
          maxHeight: null,
          focus: false ,
          lang: 'ko-KR'
        });
        
        let vm = this.bbsViewModel;
        Axios.post("/api/admin/board/mng", {})
        .then(function(response) {
          let rs = response.data;
          vm.listRoles = rs.listRoles;
          vm.listBBS = rs.listMaster;
          for(var i = 0 ; i < vm.listBBS.length ; i++){
            if(vm.listBBS[i].bbs_info_html==null || vm.listBBS[i].bbs_info_html==""){
              vm.listBBS[i].header_yn = "미사용";
            }else{
              vm.listBBS[i].header_yn = "사용";
            }
          }
        }).catch(function(ex) {
            console.log(ex);
        });
      }

      ,toggleReadSelection: function(param){
        let vm = this.bbsViewModel;
        var idx = vm.readRoleItem.indexOf(param);
        if (idx > -1) {
          vm.readRoleItem.splice(idx, 1);
        }else{
          vm.readRoleItem.push(param);
        }
      }

      ,toggleWriteSelection: function(param){
        let vm = this.bbsViewModel;
        var idx = vm.writeRoleItem.indexOf(param);
        if (idx > -1) {
          vm.writeRoleItem.splice(idx, 1);
        }else{
          vm.writeRoleItem.push(param);
        }
      }

      ,fnOnCreateAction: function(){
        let vm = this;
        if(vm.bbsViewModel.selectedBoard.bbs_id == null || vm.bbsViewModel.selectedBoard.bbs_id == ""){
          swal("게시판 아이디를 입력하세요");
          return false;
        }
        if(vm.bbsViewModel.selectedBoard.bbs_title == null || vm.bbsViewModel.selectedBoard.bbs_title == ""){
          swal("게시판 이름을 입력하세요");
          return false;
        }
        for(var i = 0 ; i < vm.bbsViewModel.readRoleItem.length ; i ++){
          if(i==0) vm.bbsViewModel.selectedBoard.read_role_id = vm.bbsViewModel.readRoleItem[i];
          else vm.bbsViewModel.selectedBoard.read_role_id+=","+vm.bbsViewModel.readRoleItem[i];
        }
        for(var i = 0 ; i < vm.bbsViewModel.writeRoleItem.length ; i++){
          if(i==0) vm.bbsViewModel.selectedBoard.write_role_id = vm.bbsViewModel.writeRoleItem[i];
          else vm.bbsViewModel.selectedBoard.write_role_id += "," + vm.bbsViewModel.writeRoleItem[i];
        }         
        vm.bbsViewModel.selectedBoard.bbs_info_html=$('#xp-summernote').summernote('code');
        
       /*  let listCategorys = new Array();
        for ( var i = 0; i < vm.bbsViewModel.listCategory.length; i++ ) {
          listCategorys.push(vm.bbsViewModel.listCategory[i].categoryTitle);
        } */

        Axios.post("/api/admin/board/create", {detailMaster:vm.bbsViewModel.selectedBoard})
        .then(function(response) {
          vm.bbsViewModel.selectedBoard = {};
          vm.bbsViewModel.readRoleItem = [];
          vm.bbsViewModel.writeRoleItem = [];
          //vm.bbsViewModel.listCategory = [];
          vm.isCreateBox = false;
          vm.isHeaderBox = false;
          vm.fnMngBBS();
        }).catch(function(ex) {
            console.log(ex);
        });
      }

      ,fnOnUpdateAction: function(){
        let vm = this;
        vm.isUpdate = false;
        for(var i = 0 ; i < vm.bbsViewModel.readRoleItem.length ; i ++){
          if(i==0) vm.bbsViewModel.selectedBoard.read_role_id = vm.bbsViewModel.readRoleItem[i];
          else vm.bbsViewModel.selectedBoard.read_role_id+=","+vm.bbsViewModel.readRoleItem[i];
        }
        for(var i = 0 ; i < vm.bbsViewModel.writeRoleItem.length ; i++){
          if(i==0) vm.bbsViewModel.selectedBoard.write_role_id = vm.bbsViewModel.writeRoleItem[i];
          else vm.bbsViewModel.selectedBoard.write_role_id += "," + vm.bbsViewModel.writeRoleItem[i];
        }
        vm.bbsViewModel.selectedBoard.bbs_info_html=$('#xp-summernote').summernote('code');
        if(vm.bbsViewModel.selectedBoard.bbs_id == null || vm.bbsViewModel.selectedBoard.bbs_id == ""){
          swal("게시판 아이디를 입력하세요");
          return false;
        }
        if(vm.bbsViewModel.selectedBoard.bbs_title == null || vm.bbsViewModel.selectedBoard.bbs_title == ""){
          swal("게시판 이름을 입력하세요");
          return false;
        }

        Axios.post("/api/admin/board/update", {detailMaster:vm.bbsViewModel.selectedBoard})
        .then(function(response) {
          vm.bbsViewModel.selectedBoard = {};
          vm.bbsViewModel.readRoleItem = [];
          vm.bbsViewModel.writeRoleItem = [];
          vm.isCreateBox = false;
          vm.isHeaderBox = false;
          vm.fnMngBBS();
        }).catch(function(ex) {
            console.log(ex);
        });
      }

      ,fnClickDetail: function(item){
        let vm = this;
        vm.isCreateBox = true;
        vm.isUpdateButton = true;

        vm.bbsViewModel.readRoleItem = [];
        vm.bbsViewModel.writeRoleItem = [];

        Axios.post("/api/admin/board/detail", {id:item.bbs_id})
        .then(function(response) {
          let rs = response.data;
          vm.bbsViewModel.selectedBoard = rs.detailMaster;
          if(vm.bbsViewModel.selectedBoard.bbs_info_html!=null && vm.bbsViewModel.selectedBoard.bbs_info_html!=""){
            vm.isHeaderBox = true;
            $('#xp-summernote').summernote("code", vm.bbsViewModel.selectedBoard.bbs_info_html);
          }
          if(rs.detailMaster.read_role_id!=null) vm.bbsViewModel.readRoleItem = rs.detailMaster.read_role_id.split(',');
          if(rs.detailMaster.write_role_id!=null) vm.bbsViewModel.writeRoleItem = rs.detailMaster.write_role_id.split(',');
        }).catch(function(ex) {
            console.log(ex);
        });
      }

      ,fnOnAddCategory: function(){
        this.bbsViewModel.selectedBoard.listCate.push({category_title:''});
      }
      
      ,fnOnDeleteCategory:function(paramIndex){
        this.bbsViewModel.selectedBoard.listCate.splice(paramIndex,1);

      }
    }
  }
</script>




