<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">게시물관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">게시판</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row"> 
        <div class="col-lg-12">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item">
                  <h5 class="mt-2 mb-0 text-black">게시물 수정</h5>
                </li>
                <li class="list-inline-item float-right">
                  <button type="button" class="btn btn-success" v-on:click="fnOnUpdateSubmit">수정</button>
                  <router-link tag="button" to='/bbsContentList' class="btn btn-warning">취소</router-link>
                </li>
              </ul>
            </div>
            <div class="card-body">
              <form name="mainForm" id="mainForm" action="/admin/board/content/update" method="post" enctype="multipart/form-data">
                <div class="form-group row">
                  <div class="col-sm-2">상단고정</div>
                  <div class="col-sm-10">
                    <div class="custom-control custom-checkbox">                      
                      <input class="custom-control-input" type="checkbox" id="headerCheck" value="Y" name="detailContent.fixed_yn" :checked="bbsContentViewModel.detailContent.fixed_yn=='Y'">
                      <label class="custom-control-label" for="headerCheck">등록</label>
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <div class="col-sm-2">게시판</div>
                  <div class="col-sm-10">
                    <select class="form-control" v-model="bbsContentViewModel.detailContent.bbs_id" name="detailContent.bbs_id">
                      <option value="">게시판을 선택하세요</option>
                      <option v-for="item in bbsContentViewModel.listBBS" :key="item.bbs_id" :value="item.bbs_id">{{item.bbs_title}}</option>
                    </select>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="bbsId" class="col-sm-2 col-form-label">제목</label>
                  <div class="col-sm-10">
                    <input type="text" class="form-control" id="bbs_cont_title" name="detailContent.bbs_cont_title" v-model="bbsContentViewModel.detailContent.bbs_cont_title">
                  </div>
                </div>
                <div class="form-group row">
                  <label for="bbsId" class="col-sm-2 col-form-label">말머리</label>
                  <div class="col-sm-10">
                    <select class="form-control" v-model="bbsContentViewModel.detailContent.category_seq" name="detailContent.category_seq">
                      <option value="">말머리를 선택하세요</option>
                      <option v-for="item in bbsContentViewModel.listCategory" :key="item.category_seq" :value="item.category_seq">{{item.category_title}}</option>
                    </select>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="bbsTitle" class="col-sm-2 col-form-label">내용</label>
                  <div class="col-sm-10">
                    <div id="xp-summernote"></div>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="bbsTitle" class="col-sm-2 col-form-label">파일 관리</label>
                  <div class="col-sm-10">
                    <div v-for="item in bbsContentViewModel.detailContent.listFiles" :key="item.file_seq" class="m-b-5">
                      <button type="button" class="btn btn-outline-secondary" v-on:click="fnFileDownload(item.file_seq)"><i class="mdi mdi-download mr-2"></i> {{item.file_org_name}}</button>
                      <button type="button" class="btn btn-outline-danger" v-on:click="fnOnFileDelete(item.file_seq)"><i class="mdi mdi-delete-sweep"></i></button>
                    </div>
                  </div>
                </div>
                <div class="form-group row">
                  <label for="bbsId" class="col-sm-2 col-form-label">개별파일 등록</label>
                  <div class="col-sm-10">
                    <div class="row">
                      <div class="col-form-label col-sm-1 pt-0"><button type="button" class="btn btn-outline-success" id="btnFileAdd"><i class="mdi mdi-plus"></i> </button></div>
                      <div class="col-sm-11"  id="fileLayer">
                        <div class="row">
                          <div class="form-group col-sm-10">
                            <input type="file" id="uploadFiles" name="uploadFiles" class="form-control">
                          </div>
                          <div class="form-group col-sm-2">
                            <button type="button" class="btn btn-outline-danger" id="btnFileDel"><i class="mdi mdi-delete-sweep"></i></button>
                          </div>                          
                        </div>                        
                      </div>
                    </div>
                  </div>
                </div>
                <input type="hidden" name="smartEditor" id="smartEditor">
            <input type="hidden" name="detailContent.file_grp_id" id="file_grp_id" :value="bbsContentViewModel.detailContent.file_grp_id">
            <input type="hidden" name="detailContent.file_grp_id_now" :value="bbsContentViewModel.detailContent.file_grp_id">
            <input type="hidden" name="id" id="id" :value="bbsContentViewModel.detailContent.bbs_id">
            <input type="hidden" name="paramSeq" id="paramSeq" :value="bbsContentViewModel.detailContent.bbs_seq">
              </form> 
              <div class="form-group row">
                <label for="bbsTitle" class="col-sm-2 col-form-label">멀티파일 등록</label>
                <div class="col-sm-10">
                  <button type="button" id="sbmtbtn" class="btn btn-secondary btn-sm btn-block" v-show="false">파일을 최종 선택 후 눌려주세요</button>
                  <form action="/api/file/upload" id="mydropzone" class="dropzone" method="post" enctype="multipart/form-data">
                    <div id="dropzonePreview"></div>
                    <input type="hidden" id="fileGroup" name="fileGroup"> 
                  </form>
                </div>
            </div>
            <div class="card-footer bg-white">
              <div class="form-group row">
                <div class="col-sm-10"> 
                  <button type="button" class="btn btn-success" v-on:click="fnOnUpdateSubmit">수정</button>
                  <router-link tag="button" to='/bbsContentList' class="btn btn-warning">취소</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Common from './common.js';

export default{
    data: function () {
      return {
        bbsContentViewModel: {
          listBBS: [],
          detailContent: {bbs_id:''},
          listCategory:[]
        },
        isMulti:false
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
      console.log("mounted");
      try {
        this.fnContentDetailInit(this.$route.params.bbsID,this.$route.params.seq);
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
      fnContentDetailInit: function(paramID,paramSeq){
        let vm = this;
        
        $("#mydropzone").dropzone();
        $('#xp-summernote').summernote({
          height: 220,
          minHeight: null,
          maxHeight: null,
          focus: false ,
          lang: 'ko-KR'
        });

        vm.bbsContentViewModel.detailContent.bbsID=paramID;
		    vm.fnDetailContent(paramID,paramSeq);
        
      }
      
      //수정 화면
      ,fnDetailContent:function(paramID,paramSeq){
        let vm = this;

        //게시판 리스트 불러오기
        Axios.post("/api/admin/board/list", {})
        .then(function(response) {
          let rs = response.data;
          vm.bbsContentViewModel.listBBS = rs.listMaster;
        }).catch(function(ex) {
            console.log(ex);
        });

        Axios.post("/api/admin/board/content/detail",{id:paramID,paramSeq:paramSeq})
        .then(function(response) {
          vm.bbsContentViewModel.detailContent=response.data.detailContent;
          vm.bbsContentViewModel.listCategory=response.data.listCategory;
          vm.bbsContentViewModel.detailContent.create_date=Common.fnConvertDateStr(response.data.detailContent.create_date);
          $('#xp-summernote').summernote({
            height: 220,
            minHeight: null,
            maxHeight: null,
            focus: false ,
            lang: 'ko-KR',
            callbacks: {
              onPaste: function (e) {
                var bufferText = ((e.originalEvent || e).clipboardData || window.clipboardData).getData('Text');
                e.preventDefault();
                //Firefox fix
                setTimeout(function () {
                  document.execCommand('insertText', false, bufferText);
                }, 10);
              }
            }
          });

          //$("#smartEditor").val($('#xp-summernote').summernote('code', vm.bbsContentViewModel.bbs_cont));
          $('#xp-summernote').summernote('code', vm.bbsContentViewModel.detailContent.bbs_cont);
          
        });
      }
      
      //수정버튼 (등록)
      , fnOnUpdateSubmit: function () {
        let vm = this;
        if(confirm("수정 하시겠습니까?")){
          if(vm.bbsContentViewModel.detailContent.bbs_cont_title == null || vm.bbsContentViewModel.detailContent.bbs_cont_title == ""){
            alert("제목을 입력하세요");
            return false;
          }
          $("#smartEditor").val($('#xp-summernote').summernote('code'));
          if($('#fileGroup').val()!=""){
            $("#file_grp_id").val($('#fileGroup').val());
          }

          let fileUploadType = ['doc','xlsx','hwp','txt','jpg','gif','png','jpeg','xml','pdf','ppt','pptx','docx','xls','bmp','zip'];
          let fileBoo = true;
          for(let i=0; i<document.mainForm.uploadFiles.length; i++){
            let fileObj = document.mainForm.uploadFiles[i];
            if(fileObj.files.length>0){
              let fileName = fileObj.files[0].name;
              let files = fileName.split('.');
              let fileExt = files[files.length-1];
              if(fileUploadType.indexOf(fileExt) == -1){
                fileBoo = false;
              }
            }
          }
          if(fileBoo == false){
            alert('업로드 불가능한 파일형식이 있습니다.');
            return;
          }
          document.forms['mainForm'].submit();
          alert("수정 되었습니다.");
        }
      }

      // 파일 삭제
      ,fnOnFileDelete:function(paramSeq){
        let vm = this;
        if(confirm("삭제 하시겠습니까?")){
          Axios.post("/api/admin/board/file/delete",{id:paramSeq})
          .then(function(response) {
            vm.bbsContentViewModel.detailContent.listFiles=response.data.listAttachFiles;
            if(response.data.listAttachFiles==null || response.data.listAttachFiles.length==0){
              vm.bbsContentViewModel.detailContent.file_grp_id=null;
            }
          });
        }
      }

      //파일 다운로드
      ,fnFileDownload: function(paramSeq){
        Common.fnOnFileDownload(paramSeq);
      }




    }
  }
</script>




