<template>
  <div>
    <div class="xp-breadcrumbbar text-center">
      <h4 class="page-title">게시물관리</h4>
      <ol class="breadcrumb">
        <li class="breadcrumb-item"><a href="#">Noaa Admin</a></li>
        <li class="breadcrumb-item active" aria-current="page">게시판</li>
      </ol>
    </div>
    <div class="xp-contentbar">
      <div class="row"> 
        <div class="col-lg-12">
          <div class="card m-b-30">
            <div class="card-header bg-white">
              <ul class="list-inline mb-0">
                <li class="list-inline-item">
                  <h5 class="mt-2 mb-0 text-black">게시물 보기</h5>
                </li>
              </ul>
            </div>
            <div class="xp-email-rightbar">
              <div class="card email-open-box m-b-30">
                <div class="card-body">                                    
                  <div class="row m-b-0">
                    <div class="col-md-5">
                      <div class="media">
                        <div class="media-body">
                          <h6 class="mt-0 text-black">
                            <a href="#"><i class="mdi mdi-star font-14" v-show="bbsContentViewModel.detailContent.fixed_yn=='Y'"></i></a>
                            <a href="#"><i class="mdi mdi-star-outline font-14" v-show="bbsContentViewModel.detailContent.fixed_yn=='N'"></i></a>
                            {{bbsContentViewModel.detailContent.bbs_cont_title}}
                            <span class="badge badge-default">{{bbsContentViewModel.detailContent.category_title}}</span>
                          </h6>
                        </div>
                      </div>
                    </div>
                    <div class="col-md-7">
                      <div class="open-email-head">
                        <ul class="list-inline mb-0">
                          <li class="list-inline-item">{{bbsContentViewModel.detailContent.create_user_name}}</li>
                          <li class="list-inline-item">{{bbsContentViewModel.detailContent.create_date}}</li>
                        </ul>
                      </div>
                    </div>
                  </div>
                  <hr>
                  <div class="row">
                    <div class="col-md-12">
                      <div v-html="bbsContentViewModel.detailContent.bbs_cont"></div>                      
                    </div>
                  </div>
                </div>
                <div class="card-footer bg-white">
                  <div class="open-email-footer">
                    <div class="row m-b-30">
                      <div class="col-md-12 m-b-5" v-for="item in bbsContentViewModel.detailContent.listFiles" :key="item.file_seq">
                        <button type="button" class="btn btn-outline-secondary" v-on:click="fnFileDownload(item.file_seq)"><i class="mdi mdi-download mr-2"></i> {{item.file_org_name}}</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="card-footer bg-white">
              <div class="form-group row">
                <div class="col-sm-10"> 
                  <router-link tag="button" :to="{name: 'bbsContentReply', params: { bbsID: bbsContentViewModel.detailContent.bbs_id , seq: bbsContentViewModel.detailContent.bbs_seq, title: bbsContentViewModel.detailContent.bbs_cont_title, secret: bbsContentViewModel.detailContent.secret_yn}}" class="btn btn-success">답글</router-link>
                  <router-link tag="button" :to="{name: 'bbsContentUpdate', params: { bbsID: bbsContentViewModel.detailContent.bbs_id , seq: bbsContentViewModel.detailContent.bbs_seq }}" class="btn btn-success">수정</router-link>
                  <router-link tag="button" to='/bbsContentList' class="btn btn-warning">목록</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Common from './common.js';

export default{
    data: function () {
      return {
        bbsContentViewModel: {
          detailContent: {bbs_id:''}
        }
      }
    }
    , created: function () {
      // console.log("created");
    }
    , mounted: function () {
      console.log("mounted");
      try {
        this.fnInit(this.$route.params.bbsID,this.$route.params.seq);
      } catch (e) {
        console.log(e);
      }
    }
    , methods: {
      fnInit: function(paramID,paramSeq){
        let vm = this;
        Axios.post("/api/admin/board/content/detail", {id:paramID,paramSeq:paramSeq})
        .then(function(response) {
          let rs = response.data;
          vm.bbsContentViewModel.detailContent = rs.detailContent;          
          vm.bbsContentViewModel.detailContent.create_date = Common.fnConvertDateStr(vm.bbsContentViewModel.detailContent.create_date,'-');
        }).catch(function(ex) {
            console.log(ex);
        });
      } 

      ,fnFileDownload: function(paramSeq){
        Common.fnOnFileDownload(paramSeq);
      }


    }
  }
</script>




